@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Rubik&display=swap);

.loading{
    display: flex;
    justify-content:center;
    align-items:center;

}

.loading svg{
    font-size:5px;
    font-weight:600;
    text-transform:uppercase;
    letter-spacing:1px;
    -webkit-animation:text 1s ease-in-out infinite;
            animation:text 1s ease-in-out infinite;

}


@-webkit-keyframes text {
    50%{opacity: 0.1;}
}


@keyframes text {
    50%{opacity: 0.1;}
}
/* Header search */

.header .search_form{
    position: relative ;
}


.header .search_form #search{
        background: #fafafa;
        border: 1px solid #ddd;
        min-width: 250px;
        width: 100%;
        outline: none;
        text-indent: 5px;
        border-radius: 3px;


}



.header .search_form #search_icon{
    position: absolute ;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.messagexyz{
    width:100%;
    height:calc(100vh-100px);
    border:1px solid #ddd;
    margin-top: 15px;
    border-radius: 3px;
    background: #fbfbfb;
}
.message_header{
    width:100%;
    height:60px;
    border-bottom:1px solid #ddd;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #f8f8f8;
}

.message_header input{
    flex: 1 1;
    height: 100%;
    border: none;
    outline: none;
    background: #f8f8f8;
    padding: 0 5px;
}


.message_chat_list{
    width:100%;
    height:calc(100%-60px);
    overflow-y: auto;

}

.message_chat_list .message_user{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
    border:1px solid #f3f3f3;
    color: #555;
    cursor: pointer;
}

.message_chat_list .message_user:hover{
    text-decoration: none;
}

.message_chat_list .message_user .fa-circle{
    font-size: 8px;
    color: #aaa;
}


.message_chat_list .message_user .fa-circle.active{
    font-size: 8px;
    color: forestgreen;
}


/* chat input */
.chat_input{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
    border: 1px solid #ddd;
}

.chat_input input{
    width: 100%;
    height: 49px;
    border: none;
    outline: none;

}

.chat_input button{
    border: none;
    outline: none;
    background-color: white;
}


/* chat container */

.chat_container{
    width: 100%;
    height: calc(100%-110px);
    overflow-y:auto ;
    padding: 0 10px;
}

.chat_display{
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}


.chat_row{
    display: grid;
    grid-template-columns: 70%;
    margin: 10px;
}


.your_message{
    justify-content: end;
    justify-items: end;
}


.other_message{
    justify-content: start;
    justify-items: start;
}

.chat_text
{
    padding:9px 14px;
    margin-bottom: 5px;
}

.chat_time{
    font-size: 13px;
    color: #777;
}

.your_message .chat_text{
    background-color: #128CED;
    color: white;
    border: 1px solid #128CED;
    border-radius: 14px 14px  14px;
}

.other_message .chat_text{
    background-color: white;
    color: black;
    border: 1px solid #ddd;
    border-radius: 14px 14px 0 14px;
}

.chat_title{
    margin-bottom: 3px;
}
/* Status */

.status{
    background-color: white;
    box-shadow: 0 0 5px #ddd;
    padding: 20px;
    border-radius: 5px;
}

.status .statusBtn{
background-color: #f1f1f1;
border: none;
outline: none;
border-radius: 30px;
color: #555;
padding: 0 10px;
text-align: left;
margin: 0 5px;
}

.status .statusBtn:hover{
    background-color: #ddd;
}
.status_modal{
position:fixed;
top:0;
left:0;
background-color: #0008;
z-index: 4;
width: 100%;
height: 100vh;
overflow: auto;

}

.status_modal form{
    max-width:450px;
    width: 100%;
    background: white;
    margin: 30px auto;
    padding: 20px;
    border-radius: 5px;

}


.status_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
    padding-bottom: 15px ;
    margin-bottom:10px ;
    -webkit-transform: translateY(-13px);
            transform: translateY(-13px);
}

 .status_header span{
    font-size: 2rem;
    font-weight: 900;
    cursor: pointer;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}

 .status_body textarea{
    width: 100%;
    min-height: 150px;
    border: none;
    outline: none;
    resize: none;
}

.status_body .input_images{
    display: flex;
    justify-content: center;
    margin: 10px 0;
}


.status_body .input_images i{
    font-size: 2rem;
    cursor: pointer;
}

 .status_body  .input_images .file_upload{
    overflow: hidden;
    margin: 0 10px;
    position: relative;

}


 .status_body .input_images .file_upload #file{
position: absolute;
top: 0;
left: 0;
opacity: 0;
}

.status_body .show_images{
    max-height: 250px;
    width: 100%;
    overflow-y: auto;
    overflow-x: hiddden;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(100px,1fr));
    place-items: ccenter;
    margin: 10px 0;

    

}

.status_body .show_images #file_img{
    position: relative;
    width: 100%;
    object-fit: contain;
    height: 100%;
    max-height: 100px;

}

.status_body .show_images #file_img img{
        display: block;
        object-fit: contain;
        width: 100%;
        height: 100%;
}



.status_body .show_images #file_img span{
        position: absolute;
        top:-1px;
        right: -2px;
        z-index: 4;
        background: white;
        color: crimson;
        padding: 3px 7px;
        border: 1px solid crimson;
        border-radius: 50%;
        font-size: 10px;
        font-weight: bolder;
        cursor: pointer;

}

.status_body .stream span{
    position: absolute;
    top: -25px;
    right: 5px;
    color: crimson;
    font-size: 2rem;
    font-weight: 900;
    cursor: pointer;


}
.post .card_icon_menu{
    display: flex;
    justify-content: space-around;
    padding: 0 15px;
    cursor:pointer;

}

.post .card_icon_menu{
    display: flex;
    justify-content: space-between;
    padding: 0 15px;
    cursor: pointer;
}

.post .card_icon_menu i{
    font-size:28px;
    margin:10px;

}



.post .card_icon_menu img{
    width: 28px;
    height: 27px;
    -webkit-transform: translateY(5px,-5px);
            transform: translateY(5px,-5px);
    opacity: 0.9;
}

/* Comments  */

.comment_display{
    padding:10px 25px;
}

.comment_card .comment_content{
    background: white;
    padding: 7px;
    border-radius: 10px;
    border-top-left-radius: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;


}

.comment_content span{
word-break: break-all;
}
.comment_card .comment_content .readMore{
    cursor: pointer;
    color: #128ced;
}

.comment_card .comment_content .nav-item{
    cursor: pointer;
    opacity: 0;
}

.comment_card .comment_content:hover .nav-item{
    opacity: 1;
}

.comment_card .comment_content .dropdown-menu{
    position:absolute;
    right: 0;
    left: inherit;
}

.comment_card .comment_content .dropdown-item{
   display: flex;
   margin: 10px 0px;
}

.comment_card .comment_content textarea{
    width: 100%;
    border: none;
    outline: none;
    background: #f1f1f1;
}

