  :root {
    --biggest-font-size: 2.125rem;
    --h2-font-size: 1.5rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
    --tiny-font-size: .688rem;
  }
}

/*==================== BASE ====================*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Lato', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  background-color: hsl(210, 100%, 99%);
  background-color: var(--body-color);
  color: hsl(210, 12%, 35%);
  color: var(--text-color);
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*==================== REUSABLE CSS CLASSES ====================*/
.container {
  max-width: 1024px;
  margin-left: 1.5rem;
  margin-left: var(--mb-1-5);
  margin-right: 1.5rem;
  margin-right: var(--mb-1-5);
}

.grid {
  display: grid;
}

/*==================== CARD PRICING ====================*/
.card {
  padding: 3rem 0;
}

.card__container {
  grid-gap: 3rem 1.25rem;
  gap: 3rem 1.25rem;
}

.card__content {
  position: relative;
  background-color: #FFF;
  background-color: var(--container-color);
  padding: 2rem 1.5rem 2.5rem;
  border-radius: 1.75rem;
  box-shadow: 0 12px 24px hsla(210, 61%, 16%, 0.1);
  box-shadow: 0 12px 24px hsla(var(--hue-color), 61%, 16%, 0.1);
  transition: .4s;
}

.card__content:hover {
  box-shadow: 0 16px 24px hsla(210, 61%, 16%, 0.15);
  box-shadow: 0 16px 24px hsla(var(--hue-color), 61%, 16%, 0.15);
}

.card__header-img {
  width: 30px;
  height: 30px;
}

.card__header-circle {
  width: 40px;
  height: 40px;
  background-color: hsl(210, 14%, 96%);
  background-color: var(--first-color-lighter);
  border-radius: 50%;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-1);
  place-items: center;
}

.card__header-subtitle {
  display: block;
  font-size: .75rem;
  font-size: var(--smaller-font-size);
  color: hsl(210, 12%, 65%);
  color: var(--text-color-light);
  text-transform: uppercase;
  margin-bottom: .25rem;
  margin-bottom: var(--mb-0-25);
}

.card__header-title {
  font-size: 1.75rem;
  font-size: var(--biggest-font-size);
  color: hsl(210, 12%, 15%);
  color: var(--title-color);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-1);
}

.card__pricing {
  position: absolute;
  background: linear-gradient(157deg, hsl(210, 96%, 69%) -12%, hsl(210, 96%, 54%) 109%);
  background: linear-gradient(157deg, var(--first-color-light) -12%, var(--first-color) 109%);
  width: 60px;
  height: 88px;
  right: 1.5rem;
  top: -1rem;
  padding-top: 1.25rem;
  text-align: center;
}

.card__pricing-number {
  font-family: 'Rubik', sans-serif;
  font-family: var(--pricing-font);
}

.card__pricing-symbol {
  font-size: .75rem;
  font-size: var(--smaller-font-size);
}

.card__pricing-number {
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
}

.card__pricing-month {
  display: block;
  font-size: .625rem;
  font-size: var(--tiny-font-size);
}

.card__pricing-number, 
.card__pricing-month {
  color: #FFF;
  color: var(--white-color);
}

.card__pricing::after, 
.card__pricing::before {
  content: '';
  position: absolute;
}

.card__pricing::after {
  width: 100%;
  height: 14px;
  background-color: #FFF;
  background-color: var(--white-color);
  left: 0;
  bottom: 0;
  -webkit-clip-path: polygon(0 100%, 50% 0, 100% 100%);
          clip-path: polygon(0 100%, 50% 0, 100% 100%);
}

.card__pricing::before {
  width: 14px;
  height: 16px;
  background-color: hsl(210, 96%, 37%);
  background-color: var(--first-color-alt);
  top: 0;
  left: -14px;
  -webkit-clip-path: polygon(0 100%, 100% 0, 100% 100%);
          clip-path: polygon(0 100%, 100% 0, 100% 100%);
}

.card__list {
  grid-row-gap: .5rem;
  row-gap: .5rem;
  margin-bottom: 2rem;
  margin-bottom: var(--mb-2);
}

.card__list-item {
  display: flex;
  align-items: center;
}

.card__list-icon {
  font-size: 1.5rem;
  color: hsl(210, 96%, 54%);
  color: var(--first-color);
  margin-right: .5rem;
  margin-right: var(--mb-0-5);
}

.card__button {
  padding: 1.25rem;
  border: none;
  font-size: .938rem;
  font-size: var(--normal-font-size);
  border-radius: .5rem;
  background: linear-gradient(157deg, hsl(210, 96%, 69%) -12%, hsl(210, 96%, 54%) 109%);
  background: linear-gradient(157deg, var(--first-color-light) -12%, var(--first-color) 109%);
  color: #FFF;
  color: var(--white-color);
  cursor: pointer;
  transition: .4s;
}

.card__button:hover {
  box-shadow: 0 12px 24px hsla(210, 97%, 54%, 0.2);
  box-shadow: 0 12px 24px hsla(var(--hue-color), 97%, 54%, 0.2);
}

/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width: 320px) {
  .container {
    margin-left: 1rem;
    margin-left: var(--mb-1);
    margin-right: 1rem;
    margin-right: var(--mb-1);
  }
  .card__content {
    padding: 2rem 1.25rem;
    border-radius: 1rem;
  }
}

/* For medium devices */
@media screen and (min-width: 568px) {
  .card__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .card__content {
    grid-template-rows: repeat(2, -webkit-max-content);
    grid-template-rows: repeat(2, max-content);
  }
  .card__button {
    align-self: flex-end;
  }
}

/* For large devices */
@media screen and (min-width: 968px) {
  .container {
    margin-left: auto;
    margin-right: auto;
  }
  .card {
    height: 100vh;
    align-items: center;
  }
  .card__container {
    grid-template-columns: repeat(3, 1fr);
  }
  .card__header-circle {
    margin-bottom: 1.25rem;
    margin-bottom: var(--mb-1-25);
  }
  .card__header-subtitle {
    font-size: .813rem;
    font-size: var(--small-font-size);
  }
}

